<template>
    <div class="Collectioncontenttest">
      
        <Collectioncontenttesttitle :goStateurl="goStateurl"></Collectioncontenttesttitle>
        <router-view @changeData="changeDatafn" :AnswerGroups="AnswerGroups"></router-view>
        <Collectionfooter @changeData="changeDatafn" :AnswerGroups="AnswerGroups"></Collectionfooter>
    </div>
</template>

<script>
    import Collectioncontenttesttitle from '../Practicecompent/Practicetitle'
    import Collectionfooter from './Collectionfooter'
    import driveanswer from '../../../public/driveanswer.json'

    export default {
        name: 'Collectioncontenttest',
        components: {
            Collectioncontenttesttitle,
            Collectionfooter,
     
        },
        methods: {
            changeDatafn(keyname, str) {
                if (keyname==='Answertrue'||keyname==='Answerfalse'||keyname==='Answerno') {
                   console.log(this.AnswerGroups[keyname]++) 
                   return
                }
                if (keyname==='empty') {
                    this.AnswerGroups.AnswerLength=[]
                    return
                }
                if (keyname === 'AnswerLength') {
                    this.AnswerGroups.AnswerLength.push(str)
                    return
                } else {
                    this.AnswerGroups[keyname] = str
                    return
                }
            }
        },
        data() {
            return {
                goStateurl: '/collection',
                AnswerGroups: {
                    Answertrue: 0,
                    Answerfalse: 0,
                    Answerno: 0,
                    AnswerId: null,
                    AnswerLength: [],
                    Answer:null,
                    Answerurl:null,
                    Expalins:null,
                    //获取文件
                    Driveanswer: driveanswer,
                    //获取长度
                    CollectionNum: Array.from(new Set(window.localStorage.CollectionQuestion.split(','))).length,
                    question: null
                },
            }
        },
    }
</script>